.App {
  text-align: center;
  width:100%;
  min-height: 100vh;
  background-image: url("assets/bg.jpg");
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;


}

body{
  width: 100vw;
}
nav{
  background: rgb(74,63,63);
background: linear-gradient(90deg, rgba(74,63,63,1) 0%, rgba(102,0,33,1) 100%);
height: fit-content;
overflow: hidden;
}

nav ul {
  display: flex;
  flex-direction: row;
  justify-content: start;
  list-style-type: none;
  min-height: 3rem;
  align-items:center;
  padding-left: 0;
}

nav ul li a :hover {
  cursor: pointer;
  color: grey;
}

.activenav {
  cursor: pointer;
  /* background-color: orange;
  color: black; */
  color:orange;
}

nav ul{
  width: 27rem;
  margin: auto;
}

nav ul li{
  margin: 0 5%;
}

nav ul li a {
  /* padding: 100% 50%; */
  text-decoration: none;
  color : white;
  font-weight: bold;
  font-size: larger;
}

@media only screen and (max-width: 480px) {
  nav ul {
    width: fit-content;
  }
}

@media only screen and (max-width: 576px) {
  .skills h3{
    display: none;
  }
}

.pfp{
  max-width: 50%;
    min-width: fit-content;
    margin: auto;
  margin: auto;
}

.pfp img {
  max-width: 250px;
}

.Home, .About, .skills, .projects {
  animation: example 3s;
}

@keyframes example {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}

.Home{
  text-align: center;
  margin-top: 5%;
}

.typingeffect{
  font-weight: bold;
}

.social i{
  padding: 20px;
  color: #46525e;
}

.social {
  margin-top: 30px;
  font-size: 40px;
}

.social i:hover{
  color: #73aae6;
}

.cimg img {
  width: 50%;
  padding: 5%;
}

.cbody{
  text-align: left;
  padding: 0% 5%;
}

.card{
  background-color: #e2e2e2;
  margin: auto;
  align-items: center;
  height: 10%;
  width: 58%;
  display: flex;
  flex-direction: row;
  border-radius: 6px;
}

.card:hover{
  box-shadow: 1px 1px 10px 2px;
}

.skills img:hover{
  filter: drop-shadow(2px 2px 2px gray);
}

.college{
  margin-top: 5%;
}

.skillutils{
  display: flex;
  flex-direction: row;
  margin: 2%;
  justify-content: space-between;
  font-size: x-large;
}

.prog{
  margin-left: 1%;
  width: 50%;
  border: 2px solid black;
}

