.accordion {
  background-color: #eee;
  color: #444;
  cursor: pointer;
  padding: 18px;
  width: 100%;
  border: none;
  text-align: left;
  outline: none;
  font-size: 15px;
  transition: 0.4s;
}

.projects .active, .accordion:hover {
  background-color: #ccc;
}

.accordion:after {
  content: '\002B';
  color: #777;
  font-weight: bold;
  float: right;
  margin-left: 5px;
}

.projects .active:after {
  content: "\2212";
}

.panel {
  padding: 0 18px;
  background-color: white;
  height: 0;
  overflow: hidden;
  transition: height 0.2s ease-out;
}

.projectacc{
  width: 80%;
  margin: auto;
  text-align: left;

}

.punit{
  margin: 3% 0%;
}

.punit button{
  border-radius:10px;
}

.pimg{
  width: 50%;
  margin: 4% auto;
}

.pimg img{
  width: 100%;
}

.prog, .exp {
  border-radius: 15px;
}

.About .aboutdesc {
  width: 60%;
  margin: auto;
}

.Home .desc{
  width: 58%;
  margin: auto;
}

.About{
  padding: 4%
}

.skills img{
  width: 70%;
  margin: 5%;
  margin-top:25%;
}

.skills h3{
  margin-top:2rem;
}

.music{
  width: 3rem;
  height: 3rem;
  position: fixed;
  right: 0;
  bottom: 50%;
  background-color: brown;
  border-radius: 7px 0 0 7px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.music img{
  width:2rem;
}

.music img:hover{
  transform: scale(1.2,1.2);
}

.music img:select{
  transform: scale(1.2,1.2);
}


.extrainfo input,textarea{
  max-width: 80%;
  margin: 2%;
  border-radius: 5px;
}

.extrainfobox{
  height: 10rem;
  margin-top: 4rem;
}

#resumebox{
  display: flex;
  justify-content: center;
  align-items: center;
} 

@media only screen and (max-width: 768px) {
    #sendmailbox{
